/* boutton qui permet de descendre au niveaux du main */
.button {
  border: 1px solid #000;
  border-radius: 62px;
  background: rgba(255, 255, 255, 0.3);
  width: 74px;
  height: 176px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.button-image-arrow {
  height: 71px;
  width: auto;
}

.button-image-butterfly {
  position: absolute;
  height: 128px;
  width: auto;
  max-width: none;
  right: -65px;
  bottom: 110px;
}

/* boutton qui revoit sur le site de la fnac */
.button-end {
  display: flex;
  padding: 10px 30px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 62px;
  border: 1px solid #000;
  background: rgba(255, 255, 255, 0.3);
  align-self: flex-end;
}

.button-end a {
  text-decoration: none;
  color: #000;
  text-align: center;
  font-family: Teachers;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.papillon-end {
  position: absolute;
  height: 100px;
  right: 24%;
  bottom: -72%;
}
